

.acts {
  &__column_right {
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }
  &__table {
    width: 570px;
    margin: 0 auto;
  }
  &__column {
    max-width: 200px;
    width: 25%;
  }
  &__summary__text {
    line-height: 14px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  &__title {
    grid-area: 1/1/2/2;
    @media all and (max-width: 800px) {
      grid-area: 1/1/2/3;
    }
  }
  &__list {
    grid-area: 2/1/4/2;
    @media all and (max-width: 800px) {
      grid-area: 3/1/4/4;
    }
  }
  &__column__text {
    margin: 12px;
    font-weight: 600;
font-size: 14px;
line-height: 17px;
  }
  &__column_right:last-child {
    padding-top: 12px;
  }
  &__icon {
    width: 10px;
    margin-left: 6px;
  }
  &__summary {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    display: flex;
    align-items: center;
    margin-bottom: 11px;
  }
  &__details{
    font-weight: 400;
    font-size: 10px;
    line-height: 120%;
    margin-top: 11px;
    max-width: 214px;
    width: 100%;
  }
  &__wrap__second {
    width: 684px;
    grid-area: 1/2/4/3;
    @media all and (max-width: 1050px) {
  width: 100%;
    }
    @media all and (max-width: 800px) {
      grid-area: 2/1/3/4;
      margin: 33px 0 22px;
    }
  }
}

details > summary::-webkit-details-marker {
  list-style-type: none;
}
details > summary::-webkit-details-marker {
  display: none;
}

details > summary {
