p,
a,
h1,
h2,
h3,
h4,
li,
tr {
  font-family: "Montserrat"; }

li {
  list-style-type: none; }

body {
  margin: 0; }

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  /* border: 1px solid red; */ }

.popup {
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  background-color: #ffffff;
  max-width: 1289px;
  width: 100%;
  margin: 0 auto; }

.container {
  max-width: 1214px;
  width: 100%;
  margin: 0 auto;
  padding: 33px 20px 0; }
  @media all and (max-width: 800px) {
    .container {
      padding: 30px 10px; } }
  .container__icons {
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.container_second {
  max-width: 1070px;
  width: 100%;
  margin: 0 auto;
  padding: 0 5px; }

a {
  text-decoration: none; }

button:hover {
  opacity: 0.9; }

p, details, summary, td {
  font-family: 'Montserrat'; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

a, summary {
  cursor: pointer; }

a {
  color: inherit; }

h2 {
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 20px;
  line-height: 26px; }

.acts__column_right {
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 10px;
  line-height: 14px; }

.acts__table {
  width: 570px;
  margin: 0 auto; }

.acts__column {
  max-width: 200px;
  width: 25%; }

.acts__summary__text {
  line-height: 14px; }

.acts__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

.acts__title {
  grid-area: 1/1/2/2; }
  @media all and (max-width: 800px) {
    .acts__title {
      grid-area: 1/1/2/3; } }

.acts__list {
  grid-area: 2/1/4/2; }
  @media all and (max-width: 800px) {
    .acts__list {
      grid-area: 3/1/4/4; } }

.acts__column__text {
  margin: 12px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px; }

.acts__column_right:last-child {
  padding-top: 12px; }

.acts__icon {
  width: 10px;
  margin-left: 6px; }

.acts__summary {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  align-items: center;
  margin-bottom: 11px; }

.acts__details {
  font-weight: 400;
  font-size: 10px;
  line-height: 120%;
  margin-top: 11px;
  max-width: 214px;
  width: 100%; }

.acts__wrap__second {
  width: 684px;
  grid-area: 1/2/4/3; }
  @media all and (max-width: 1050px) {
    .acts__wrap__second {
      width: 100%; } }
  @media all and (max-width: 800px) {
    .acts__wrap__second {
      grid-area: 2/1/3/4;
      margin: 33px 0 22px; } }

details > summary::-webkit-details-marker {
  list-style-type: none; }

details > summary::-webkit-details-marker {
  display: none; }

details > summary {
  list-style-type: none; }

details > summary::-webkit-details-marker {
  display: none; }

details[open] {
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s; }

details[open] img {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg); }

.acts__table {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  width: 100%;
  padding: 10px 20px 20px 10px; }

.container__img:last-child {
  max-width: 24px;
  height: 24px; }

details[open] summary ~ * {
  animation: sweep .5s;
  -webkit-animation: sweep .5s; }

@keyframes sweep {
  0% {
    opacity: 0;
    margin-top: -20px; }
  100% {
    opacity: 1;
    margin-top: 0px; } }

details summary ~ * {
  animation: sweep2 .5s ease-in-out;
  -webkit-animation: sweep2 .5s ease-in-out; }

@keyframes sweep2 {
  0% {
    opacity: 0;
    margin-top: -10px; }
  100% {
    opacity: 1;
    margin-top: 0px; } }

.tables {
  margin-top: 50px; }
  .tables__text {
    font-weight: 400;
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    margin-right: 10px;
    text-align: left; }
    @media all and (max-width: 460px) {
      .tables__text {
        font-size: 10px;
        line-height: 12px; } }
  .tables__text_bold {
    font-weight: 600; }
  .tables__table {
    margin-top: 19px;
